import "./styles.css";
import Top from "./Top";
import Search from "./Search";
import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";

function createData(
  name: string,
  def: number,
  one: number,
  two: number,
  three: number,
  mone: number,
  mtwo: number,
  mthree: number
) {
  return { name, def, one, two, three, mone, mtwo, mthree };
}

const rows = [
  createData("最速スカーフ", 192, 288, 384, 480, 127, 96, 76),
  createData("最速", 128, 192, 256, 320, 85, 64, 51),
  createData("準速", 117, 175, 234, 292, 78, 58, 46),
  createData("無振り", 85, 127, 170, 212, 56, 42, 34),
  createData("最遅", 63, 94, 126, 157, 42, 31, 25),
  createData("最遅てっきゅう", 31, 47, 63, 78, 21, 15, 12)
];

export default function App() {
  return (
    <div>
      <Top />

      <Search />
      <div sx={{ display: "flex" }}>
        <TableContainer align="center">
          <Table sx={{ maxWidth: "md" }} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>ニャオハ</TableCell>
                <TableCell align="right">±0</TableCell>
                <TableCell align="right">+1</TableCell>
                <TableCell align="right">+2</TableCell>
                <TableCell align="right">+3</TableCell>
                <TableCell align="right">-1</TableCell>
                <TableCell align="right">-2</TableCell>
                <TableCell align="right">-3</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => (
                <TableRow
                  key={row.name}
                  sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                >
                  <TableCell component="th" scope="row">
                    {row.name}
                  </TableCell>
                  <TableCell align="right">{row.def}</TableCell>
                  <TableCell align="right">{row.one}</TableCell>
                  <TableCell align="right">{row.two}</TableCell>
                  <TableCell align="right">{row.three}</TableCell>
                  <TableCell align="right">{row.mone}</TableCell>
                  <TableCell align="right">{row.mtwo}</TableCell>
                  <TableCell align="right">{row.mthree}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    </div>
  );
}
